import {Button, Col, Container, Form, Row, Table} from "react-bootstrap";
import PriceAlertModal from "../../components/PriceAlertModal/PriceAlertModal";
import {useState} from "react";
import {getDataByContractAddress, getDataByTokenAddress} from "../../api/dexscreenerApi";

const NewAlert = ({onAlertSave}) => {
  const [modalShow, setModalShow] = useState(false);

  const [dexData, setDexData] = useState(null);
  const [contractAddress, setContractAddress] = useState('');



  const inferval = (fn) => setInterval(() => fn, 1000);

  const handleSubmitContractAddress = (e) => {

    clearInterval(inferval());

    e.preventDefault();
    if (!contractAddress) return;

    let getFunction;

    getDataByTokenAddress(contractAddress).then(res => {
      res ? getFunction = getDataByTokenAddress : getFunction = getDataByContractAddress;
      inferval(getFunction(contractAddress).then(res => setDexData(res)));
    });
  };

  return (
    <>
      <Container fluid>
        <Row className="w-100">
          <Col>
            <Form onSubmit={handleSubmitContractAddress} className="contract-address-input-wrapper">
              <Form.Control type="text" size="lg" id='input-address' className="input" value={contractAddress}
                            onChange={e => setContractAddress(e.target.value)} placeholder="Adres kontraktu lub tokena"/>
              <br/>
              <Button variant="outline-light" type="submit">
                Pobierz
              </Button>
            </Form>
          </Col>
        </Row>

        <Row>
          <Col>
            {dexData &&
              <iframe
                id="dexscreener-embed"
                src={`https://dexscreener.com/ethereum/${contractAddress}?embed=1&theme=dark&trades=0&info=0`}>
              </iframe>}
          </Col>

          <Col>
            {dexData &&
              <>
                <Table striped bordered hover variant="dark">
                  <thead>
                  <tr>
                    <th colSpan={2}>Informacje:</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr>
                    <td>Symbol</td>
                    <td>{dexData.baseToken.name}</td>
                  </tr>
                  <tr>
                    <td>Aktualna cena</td>
                    <td>{dexData.priceUsd}$</td>
                  </tr>
                  <tr>
                    <td className="vol-td-1">Volumen</td>
                    <td className="vol-td-2">
                      24h: {parseInt(dexData.volume.h24, 10)}$<br/>
                      6h: {parseInt(dexData.volume.h6, 10)}$<br/>
                      1h: {parseInt(dexData.volume.h1, 10)}$<br/>
                      5min: {parseInt(dexData.volume.m5, 10)}$<br/>
                    </td>
                  </tr>
                  </tbody>
                </Table>

                <Button variant="outline-light" size={'lg'} onClick={() => setModalShow(true)}>
                  Dodaj powiadomienie cenowe
                </Button>

              </>
            }
          </Col>
        </Row>
      </Container>

      <PriceAlertModal
        onClose={() => setModalShow(false)}
        onAlertSave={onAlertSave}
        showModal={modalShow}
        currentPrice={dexData?.priceUsd}
      />
    </>
  )
}

export default NewAlert;