import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import NavigationBar from "./components/NavigationBar/NavigationBar";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import NewAlert from "./pages/NewAlert/NewAlert";
import {useEffect, useState} from "react";
import storage from "./utils/localStorage";
import MyAlerts from "./pages/MyAlerts/MyAlerts";

const App = () => {
  //0x6846db8Fd9165569326254bd54f50Fce8f491877
  const [connectedWallet, setConnectedWallet] = useState(null);
  const [alerts, setAlerts] = useState(null);

  useEffect(() => {
    getCurrentConnectedWallet();
    addWalletListener();
  }, [connectedWallet])

  useEffect(() => {
    if (!alerts) {
      setAlerts(JSON.parse(storage.get('alerts')));
    }
  }, [alerts])

  const connectWallet = async () => {
    if (connectedWallet) return;

    if (window?.ethereum) {
      try {
        const accounts = await window.ethereum.request({method: "eth_requestAccounts"});
        console.log(accounts)
        setConnectedWallet(accounts[0])
      } catch (err) {
        console.error(err.message)
      }
    } else {
      console.log("Install MM")
    }
  }

  const getCurrentConnectedWallet = async () => {
    if (connectedWallet) return;

    if (window?.ethereum) {
      try {
        const accounts = await window.ethereum.request({method: "eth_requestAccounts"});
        console.log(accounts)
        setConnectedWallet(accounts[0])
      } catch (err) {
        console.error(err.message)
      }
    } else {
      console.log("Install MM")
    }
  }

  const addWalletListener = async () => {
    if (connectedWallet) return;

    if (window?.ethereum) {
      window.ethereum.on("accountsChanged", (accounts) => {
        setConnectedWallet(accounts[0])
      })
    } else {
      setConnectedWallet(null);
    }
  }

  const handleAlertSave = (alertData) => {
    storage.save('alerts', JSON.stringify({...alerts, alertData}));
  }

  return (
    <div className="app-container">
      <BrowserRouter>
        <div className="nav-container">
          <NavigationBar onConnectWallet={connectWallet} connectedWallet={connectedWallet}/>
        </div>
        <Routes>
          <Route exact path="/alerts" element={<MyAlerts/>} />
          <Route exact path="/new-alert" element={<NewAlert onAlertSave={handleAlertSave}/>} />
        </Routes>
      </BrowserRouter>

    </div>
  );
}

export default App;
