import './styles.css'
import {Button, Nav} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';

const NavigationBar = ({onConnectWallet, connectedWallet}) => {
  return (
    <Nav variant="tabs" className='nav-container' defaultActiveKey="/new-alert">
      <div className='nav-items'>
        <Nav.Item>
          <LinkContainer to="/tokens">
            <Nav.Link href="/tokens" eventKey="disabled-1" disabled>Moje tokeny</Nav.Link>
          </LinkContainer>
        </Nav.Item>
        <Nav.Item>
          <LinkContainer to="/alerts">
            <Nav.Link href="/alerts" eventKey="disabled">Moje alerty</Nav.Link>
          </LinkContainer>
        </Nav.Item>
        <Nav.Item>
          <LinkContainer to="/new-alert">
            <Nav.Link href="/new-alert" eventKey="link-2">Nowy alert</Nav.Link>
          </LinkContainer>
        </Nav.Item>
        <Nav.Item>
          <LinkContainer to="/settings">
            <Nav.Link href="/settings" eventKey="disabled-1" disabled>Ustawienia</Nav.Link>
          </LinkContainer>
        </Nav.Item>
        <Nav.Item>
          <LinkContainer to="/research">
            <Nav.Link href="/research" eventKey="disabled-1" disabled>Research</Nav.Link>
          </LinkContainer>
        </Nav.Item>
      </div>
      <div>
        <Button size='lg' onClick={() => onConnectWallet()}>{ connectedWallet ? `Connected: ${connectedWallet.substring(0, 6)}...${connectedWallet.substring(38)}` : "CONNECT WALLET"}</Button>
      </div>
    </Nav>
  );
}

export default NavigationBar;