import axios from "axios";

const getDataByTokenAddress = async (address) => {
    const url = `https://api.dexscreener.com/latest/dex/tokens/${address}`

    return axios.get(url).then(res => {
        if (!res?.data?.pairs) {
            return null;
        }

        return res.data.pairs[0]
    });
}

const getDataByContractAddress = async (address, chain = 'ethereum') => {
    const url = `https://api.dexscreener.com/latest/dex/pairs/${chain}/${address}`

    return axios.get(url).then(res => {
        if (!res?.data?.pairs) {
            return null;
        }

        return res.data.pairs[0]
    });
}

export {
    getDataByTokenAddress,
    getDataByContractAddress,
}